.memory-game {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fd9998;
}



.music {
  color: white;
  display: flex;
  margin-top: 30px;
  align-items: center
}

.audio {
  display: flex;
    background-color: #f9cee1;
    border: 5px dashed #f67469;
    color: #f67469;
    cursor: pointer;
    font-size: 24px;
    padding: 16px;
    text-align: center;
    text-decoration: none;
    height: 100px;
    width: 100px;
    border-radius: 100%;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
    animation: border-animation 2s infinite;
    transition: all 0.2s;
  }

  .audio:hover {
    background-color: #f6ab69;
    color: white;
  }
  
  @keyframes border-animation {
    0% {
      border: 5px dashed #f67469;
    }
    20% {
      border: 5px dashed #f6ab69;
    }
    40% {
      border: 5px dashed #c2f669;
    }
    60% {
      border: 5px dashed #69f6f1;
    }
    80% {
      border: 5px dashed #696bf6;
    }
    100% {
      border: 5px dashed #f669a1;
    }
  }


h1 {
  margin-right: 20px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 2rem;
}

.card {
  position: relative;
  width: 180px;
  height: 180px;
  margin: 1rem;
  perspective: 1000px;
  transition: transform 0.5s ease;
  transform-style: preserve-3d;
  cursor: pointer;
  border-radius: 5px;
  transition: all 0.3s;
}

.card:hover {
  transform: scale(1.1);
}

.card.flipped {
  transform: rotateY(180deg);
}

.front,
.back {
  position: absolute;
  backface-visibility: hidden;
  border-radius: 5px;
}

.front {
  width: 180px;
  height: 180px;
  cursor: pointer;
  transition: transform 0.3s;
  background-image: url(./../../public/assets/background.jpg);
  background-position: center;
  background-size: contain;
}

.back {
  transform: rotateY(180deg);
}

.card-image {
  box-sizing: border-box;
  height: 180px;
  width: 180px;
  object-fit: cover;
  border-radius: 5px;
  transition: opacity .5s ease-out;
} 
