.wish-page {
  background-color: #e58bde;
  min-height: 100vh;
  min-width: 100vw;
  color: white;
}

.wish-block {
  padding: 20px;
}

.wish-title {
  font-size: 100px;
  display: flex;
  align-items: center;
  height: 80vh;
  justify-content: center;

}

.moving-image {
  position: absolute;
  border-radius: 20%;
  width: 300px;
  border: 2px solid white;
  z-index: 9999;
}



.wish-text {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-bottom: 50px;
  margin-right: 50px;
}

h1 {
  margin: 0;
}