.win-page {
  background-color: #0e739a;
  min-height: 100vh;
}

.card-win {
  width: 250px;
  height: 250px;
  margin: 10px;
  perspective: 800px;
  cursor: pointer;
  background-size: contain;
  background-image: url(./../../public/assets/background-win.jpg);
  background-position: center;
  background-size: contain;
  transition: all 0.3s;
  border-radius: 5px;
}

.card-win:hover{
  transform: scale(1.1);
}

.card-win-image {
  min-width: 200px;
  max-width: 300px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
}

.modal {
  position: fixed;
  top: -20%;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #0e739a;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  max-width: 90%;
  max-height: 500px;
  position: relative;
  text-align: center;
  color: white;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.close-button:hover {
  color: deeppink;
}

p,
pre {
  white-space: break-spaces;
}

h2 {
  margin: 0 auto;
  text-align: center;
  padding: 10px;
  color: white;
  font-size: 40px;
}

.next {
  display: flex;
  background: rgb(255 161 7);
  color: white;
  margin: 20px 50px;
  height: 100px;
  width: 100px;
  border-radius: 100%;
  border: 3px dotted #21475a;
  align-items: center;
  justify-content: center;
}

.button-class {
  display: flex;
  justify-content: flex-end;
}